/**
 * 
 *
 * @param {string} mobile 
 */
function toDiscontinuousMobile(mobile: string) {
    let result = mobile
    if (!result) {
        return result
    }
    result = result.replace(/[^(\d|\s)]/g, ''); //
    result = result.replace(/^(\d{1,3})\s?(\d{1,4})?\s?(\d{1,4})?$/, '$1 $2 $3'); //
    result = result.replace(/\s+$/, ''); //

    return result;
}

export default toDiscontinuousMobile;
