























import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { DictionaryItem } from "@/mode/dictionary-item";
@Component({
    name: "MyCheckbox"
})
export default class MyCheckbox extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop()
    options!: DictionaryItem[];

    @Prop({ default: "checkbox" })
    radioType!: "button" | "checkbox ";

    get valueEnum() {
        const options = this.options.map(option => {
            if (typeof option === "string") {
                return {
                    key: option,
                    label: option,
                    value: option
                };
            }
            return option;
        });
        return options;
    }
    get radioTagName() {
        return this.radioType == "button" ? "el-checkbox-button" : "el-checkbox";
    }
    get currentItem(): DictionaryItem[] {
        return this.options.filter(item => this.currentValue.includes(item.value));
    }
}
