












import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
@Component({
    name: "MyDight"
})
export default class MyDight extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    get initValue() {
        const digit = new Intl.NumberFormat().format(Number(this.currentValue) as number);
        return digit;
    }
}
