











import { Vue, Component, Mixins, Watch, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import debounce from "lodash.debounce";
import { toDiscontinuousMobile } from "utils/data";

@Component
export default class MyInputMobile extends Mixins(TwoWay) {
    @Prop({ type: Boolean, default: false })
    debounce!: boolean;

    /**  */
    inputValue = "";

    /** */
    emitValue() {
        this.$nextTick(() => {
            this.currentValue = this.inputValue.replace(/\s/g, "");
        });
    }

    /**  */
    debounceEmitValue = debounce(function(this: MyInputMobile) {
        this.$nextTick(() => {
            this.currentValue = this.inputValue.replace(/\s/g, "");
        });
    }, 500);

    @Watch("currentValue", {
        immediate: true
    })
    watchCurrentValue(newValue: any, oldValue: any) {
        this.inputValue = toDiscontinuousMobile(this.currentValue);
    }

    @Watch("inputValue")
    watchInputValue(newValue: any, oldValue: any) {
        this.$nextTick(() => {
            const result = newValue ? toDiscontinuousMobile(newValue) : newValue;
            this.inputValue = result;
            if (this.debounce) this.debounceEmitValue();
            else this.emitValue();
        });
    }
}
