



















import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { DictionaryItem } from "@/mode/dictionary-item";
@Component({
    name: "MyRadio"
})
export default class MyRadio extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop({ default: "radio" })
    radioType!: "button" | "radio";

    @Prop()
    options!: DictionaryItem[];

    get valueEnum() {
        const options = this.options.map(option => {
            if (typeof option === "string") {
                return {
                    key: option,
                    label: option,
                    value: option
                };
            }
            return option;
        });
        return options;
    }
    get radioTagName() {
        return this.radioType == "button" ? "el-radio-button" : "el-radio";
    }
    get currentItem(): DictionaryItem | undefined {
        return this.options.find((item: any) => item.value == this.currentValue);
    }
}
