










import { importAll } from "@/utils/common/require-files";
import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
type FileNmae = {
    [key: string]: string;
};
const FILEDNAME: FileNmae = {
    text: "my-text",
    avatar: "my-avatar",
    textarea: "my-textarea",
    checkbox: "my-checkbox",
    radio: "my-radio",
    digit: "my-digit",
    color: "my-color",
    password: "my-password",
    image: "my-image",
    int: "my-input-int",
    sendcode: "my-input-send-code",
    money: "my-money",
    percent: "my-percent",
    process: "my-process",
    rate: "my-rate",
    second: "my-second",
    select: "my-select",
    cascader: "my-cascader",
    status: "my-status",
    time: "my-time-select",
    date: "my-date-picker",
    week: "my-date-picker-week",
    dateRange: "my-date-range-picker",
    datetime: "my-datetime-picker",
    datetimeRange: "my-datetime-range-picker",
    timeRange: "my-time-range-picker"
};
@Component({
    components: importAll(require.context("@/resources/components", true, /\.vue$/)).default
})
export default class ProFiled extends Mixins(TwoWay) {
    /**
     * 
     */
    @Prop({ default: "edit" })
    mode!: string;
    /**
     * 
     */
    @Prop({ default: "text" })
    valueType!: string;
    /**
     * 
     */
    @Prop()
    options!: Array<any>;
    /**
     * 
     */
    @Prop()
    previewSrcList!: Array<any>;

    @Prop()
    width!: string;

    get filedName() {
        return FILEDNAME[this.valueType];
    }
}
