















import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import { DatePickerOptions } from "element-ui/types/date-picker";

@Component({
    name: "MyDatePickerWeek"
})
export default class MyDatePickerWeek extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    pickerOptions: DatePickerOptions = {
        shortcuts: [
            {
                text: "",
                onClick(picker) {
                    const date = new Date();
                    date.setDate(date.getDate() - 7);
                    picker.$emit("pick", date);
                }
            },
            {
                text: "",
                onClick(picker) {
                    const now = new Date();
                    picker.$emit("pick", now);
                }
            },
            {
                text: "",
                onClick(picker) {
                    const date = new Date();
                    date.setDate(date.getDate() + 7);
                    picker.$emit("pick", date);
                }
            }
        ]
    };
    get initValue() {
        return `${this.getYearWeek(new Date(this.currentValue))}`;
    }
    getYearWeek(dateTime: Date) {
        let temptTime = dateTime;
        //
        let weekday = temptTime.getDay() || 7;
        //1+5=
        temptTime.setDate(temptTime.getDate() - weekday + 1 + 5);
        let firstDay = new Date(temptTime.getFullYear(), 0, 1);
        let dayOfWeek = firstDay.getDay();
        let spendDay = 1;
        if (dayOfWeek != 0) {
            spendDay = 7 - dayOfWeek + 1;
        }
        firstDay = new Date(temptTime.getFullYear(), 0, 1 + spendDay);
        let d = Math.ceil((temptTime.valueOf() - firstDay.valueOf()) / 86400000);
        let result = Math.ceil(d / 7);
        return result;
    }
}
