






import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({
    name: 'MyStatus',
})
export default class MyStatus extends Vue {
    @Prop()
    type!: string;

    get className() {
        return this.type
            ? ['status-type', `status-type-${this.type}`]
            : ['empty'];
    }
}
