










import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import toNumber from "lodash.tonumber";

@Component({
    name: "MyProcess"
})
export default class MyProcess extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;
    /**
     * 
     */
    get realValue() {
        if (typeof this.currentValue === "string" && this.currentValue.includes("%")) {
            return toNumber(this.currentValue.replace("%", ""));
        }
        return this.currentValue ? toNumber(this.currentValue) : 0;
    }
}
