
/**
 * @description webpack   
 * @param context require.context('./components', true, /\.vue$/)
 *   imortAll(require.context('./components', true, /\.vue$/))
 */
export const importAll = (context: any) => {
    const map: any = {};
    for (const key of context.keys()) {
        const keyArr: any = key.split('/');
        map[keyArr[keyArr.length - 1].replace(/\.vue$/g, '')] = context(key);
    }
    return map;
};
