

















































































































































import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';

@Component({
    name: 'MyDescripts',
    components: {
        DescriptionContent: {
            props: {
                item: Object
            },
            render(h: CreateElement): VNode {
                return this.$props.item.children;
            }
        }
    }
})
export default class MyDescripts extends Vue {
    @Prop({ default: 3 })
    column!: number;
    @Prop({ default: 'horizontal' })
    layout!: string;

    @Prop()
    title!: string; // 

    @Prop()
    tooltip!: string; // 

    @Prop({ default: 'el-icon-warning-outline' })
    tooltipIcon!: string; // icon

    @Prop({ type: Boolean, default: false })
    bordered!: boolean;

    @Prop({ type: String, default: 'default' })
    size!: string;

    rows: VNode[] = [];
    refreshLock: boolean = false;
    get className() {
        return [
            {
                'descriptions-bordered': this.bordered,
                descriptions: this.size === 'default',
                'descriptions-middle': this.size === 'middle',
                'descriptions-small': this.size === 'small'
            }
        ];
    }
    @Watch('column')
    watchColumn(val: number) {
        this.generateChildrenRow(this.$slots.default || []);
    }
    mounted() {
        this.generateChildrenRow(this.$slots.default || []);
    }
    beforeUpdate() {
        /** */
        if (this.refreshLock) {
            this.generateChildrenRow(this.$slots.default || []);
        }
        this.refreshLock = true;
    }
    // 
    generateChildrenRow(dataSource: VNode[]) {
        const dataList = dataSource.filter(
            (item: VNode) => item.tag === 'my-descriptions-item'
        );
        this.rows = [];
        let leftSpan = this.column;
        let children: any[] = [];
        dataList.forEach((item: VNode, index: number) => {
            const itemAttrs = item?.data?.attrs || {};
            // 
            const isFinalCol = index === dataList.length - 1;
            // columnspan
            if (leftSpan <= (itemAttrs.span || 1) || isFinalCol) {
                // span, 
                // 
                itemAttrs.span = leftSpan;
                leftSpan = 0;
            } else {
                leftSpan -= itemAttrs.span || 1;
            }
            // 
            children.push({
                span: itemAttrs.span || 1,
                label: (item.data && itemAttrs.label) || '',
                tooltip: (item.data && itemAttrs.tooltip) || '',
                ...item
            });
            if (leftSpan <= 0) {
                leftSpan = this.column;
                children.length > 0 &&
                    this.$set(this.rows, this.rows.length, children);
                children = [];
            }
            //  
            if (dataList.length % this.column < this.column && isFinalCol) {
                children.length > 0 &&
                    this.$set(this.rows, this.rows.length, children);
            }
        });
    }
}
