

















import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import toNumber from "lodash.tonumber";
import { getColorByRealValue, getSymbolByRealValue, getRealValueWithPrecision } from "./util";

@Component({ name: "MyPercent" })
export default class MyPercent extends Mixins(TwoWay) {
    @Prop({ default: "edit" }) mode!: string;

    /** */
    @Prop({ default: 2 }) precision!: number;

    /** true, false */
    @Prop({ type: Boolean, default: false }) showColor!: boolean;

    /** true, false */
    @Prop({ type: Boolean, default: false }) showSymbol!: boolean;

    /** */
    @Prop({ default: "" }) prefix!: string;

    /** */
    @Prop({ default: "%" }) suffix!: string;

    /** */
    get style() {
        const style = this.showColor ? { color: getColorByRealValue(this.realValue) } : {};
        return style;
    }
    /**
     * 
     */
    get realValue() {
        return typeof this.currentValue === "string" && this.currentValue.includes("%")
            ? toNumber(this.currentValue.replace("%", ""))
            : toNumber(this.currentValue);
    }
    /** - +*/
    get symbol() {
        return getSymbolByRealValue(this.realValue);
    }
    get finalValue() {
        return getRealValueWithPrecision(Math.abs(this.realValue), this.precision);
    }
}
